*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 93.75%;
}

body {
  font-family: "Alata", sans-serif;
  font-size: calc(1rem + 0.75vw);
}

a {
  color: white;
  text-decoration: none;
}

a,
p {
  font-size: 0.875rem;
}
@media screen and (min-width: 999px) {
  a,
p {
    font-size: 1rem;
  }
}

h1,
h2,
h3,
h4 {
  font-family: "Alata", sans-serif;
  margin-top: 0px;
}

.container {
  max-width: 1116px;
  margin: 0 auto;
}

li {
  list-style-type: none;
}

.header {
  background: url("../../images/mobile/image-hero.jpg") no-repeat center center/cover;
  height: 100vh;
  padding: 48px 26px;
}
@media screen and (min-width: 699px) {
  .header {
    background: url("../../images/desktop/image-hero.jpg") no-repeat center center/cover;
    padding-top: 60px;
    padding-bottom: 143px;
  }
}
.header__hero nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.header__hero nav ul {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  background: #000;
  height: 100vh;
  z-index: 2;
  padding: 2em;
  transform: translateX(100%);
  transition: transform 0.3s;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul {
    width: unset;
    position: unset;
    transform: translateX(0);
    background: none;
    height: auto;
    padding-top: 1em;
  }
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links {
    display: flex;
  }
}
.header__hero nav ul .nav-links a {
  position: relative;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links a {
    margin-right: 20px;
  }
}
.header__hero nav ul .nav-links a::before {
  content: "";
  display: block;
  position: absolute;
  height: 2px;
  width: 50%;
  margin: 0 auto;
  left: 0;
  right: 0;
  bottom: -10px;
  background: #fff;
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__hero nav ul .nav-links a:hover::before {
  opacity: 1;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links .last {
    margin-right: 0px;
  }
}
.header__hero nav ul .logo {
  margin-bottom: 100px;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .logo {
    display: none;
  }
}
.header__hero nav ul .nav-links a {
  font-family: "Josefin Sans", sans-serif;
  font-size: 36px;
  text-transform: uppercase;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links a {
    font-family: "Alata", sans-serif;
    font-size: 0.875rem;
    text-transform: initial;
  }
}
.header__hero nav ul .nav-links a li {
  margin-bottom: 30px;
}
@media screen and (min-width: 699px) {
  .header__hero nav ul .nav-links a li {
    margin-bottom: 0px;
  }
}
.header__hero-content {
  margin-top: 200px;
  border: 2px solid #fff;
  text-transform: uppercase;
  padding: 32px 36px;
}
@media screen and (min-width: 699px) {
  .header__hero-content {
    max-width: 450px;
    margin-top: 126px;
  }
}
@media screen and (min-width: 999px) {
  .header__hero-content {
    max-width: 650px;
  }
}
.header__hero-content h1 {
  font-family: "Josefin Sans", sans-serif;
  color: #fff;
  font-weight: 300;
}
.header__hero-content h1 .down-text {
  display: block;
}
@media screen and (min-width: 999px) {
  .header__hero-content h1 .down-text {
    display: initial;
  }
}

.menu-btn,
.exit-btn {
  cursor: pointer;
}
@media screen and (min-width: 699px) {
  .menu-btn,
.exit-btn {
    display: none;
  }
}

.mobile-nav-head {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}

.about {
  margin: 121px 30px;
}
.about__info {
  position: relative;
}
.about__info-img {
  background: url("../../images/mobile/image-interactive.jpg") no-repeat center center/cover;
  width: 87.42%;
  height: calc(285px + 10vw);
  margin: 0 auto;
  margin-bottom: 62px;
}
@media screen and (min-width: 699px) {
  .about__info-img {
    max-width: 529px;
  }
}
@media screen and (min-width: 999px) {
  .about__info-img {
    background: url("../../images/desktop/image-interactive.jpg") no-repeat center center/cover;
    max-width: 729px;
    height: 504px;
    margin-left: 0;
  }
}
.about__info-text {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
@media screen and (min-width: 699px) {
  .about__info-text {
    width: 60%;
  }
}
@media screen and (min-width: 999px) {
  .about__info-text {
    width: 542px;
    height: 319px;
    position: absolute;
    bottom: 0;
    right: 0;
    background: #fff;
    padding-top: 95px;
    padding-left: 96px;
    text-align: unset;
  }
}
.about__info-text h2 {
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  text-transform: uppercase;
  margin-bottom: 35px;
  font-size: 30px;
}
@media screen and (min-width: 999px) {
  .about__info-text h2 {
    font-size: 36px;
  }
}
.about__info-text p {
  color: dimgray;
  font-size: 18px;
  line-height: 1.3;
}
@media screen and (min-width: 999px) {
  .about__info-text p {
    font-size: 14px;
  }
}

.creations {
  margin-bottom: 120px;
}
@media (min-width: 699px) and (max-width: 1179px) {
  .creations__info {
    margin-left: 30px;
    margin-right: 30px;
  }
}
@media screen and (min-width: 699px) {
  .creations__info-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;
  }
}
@media screen and (min-width: 999px) {
  .creations__info-header {
    margin-bottom: 90px;
  }
}
.creations__info-header h2 {
  font-size: 28px;
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 300;
  text-align: center;
  margin-bottom: 65px;
}
@media screen and (min-width: 699px) {
  .creations__info-header h2 {
    margin-bottom: 0;
    text-align: initial;
  }
}
.creations__info-grid {
  display: grid;
  gap: 25px;
  place-items: center;
}
@media screen and (min-width: 699px) {
  .creations__info-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (min-width: 999px) {
  .creations__info-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.creations__info-grid .creation {
  width: 87%;
  height: 151px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  color: #fff;
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  padding-left: 27px;
  padding-bottom: 32px;
  font-size: 30px;
  position: relative;
  transition: all 0.3s ease-in-out;
}
.creations__info-grid .creation::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}
.creations__info-grid .creation:hover {
  color: #000;
}
.creations__info-grid .creation:hover::before {
  cursor: pointer;
  background: white;
  opacity: 0.6;
}
@media screen and (min-width: 699px) {
  .creations__info-grid .creation {
    width: 100%;
    height: 300px;
  }
}
@media screen and (min-width: 699px) {
  .creations__info-grid .creation {
    width: 100%;
    height: 449px;
    padding-left: 18px;
  }
}
.creations__info-grid .creation .text {
  z-index: 10;
}
.creations__info-grid .c1 {
  background: url("../../images/mobile/image-deep-earth.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c1 {
    background: url("../../images/desktop/image-deep-earth.jpg") no-repeat center center/cover;
  }
}
.creations__info-grid .c2 {
  background: url("../../images/mobile/image-night-arcade.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c2 {
    background: url("../../images/desktop/image-night-arcade.jpg") no-repeat center center/cover;
  }
}
.creations__info-grid .c3 {
  background: url("../../images/mobile/image-soccer-team.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c3 {
    background: url("../../images/desktop/image-soccer-team.jpg") no-repeat center center/cover;
  }
}
.creations__info-grid .c4 {
  background: url("../../images/mobile/image-grid.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c4 {
    background: url("../../images/desktop/image-grid.jpg") no-repeat center center/cover;
  }
}
.creations__info-grid .c5 {
  background: url("../../images/mobile/image-from-above.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c5 {
    background: url("../../images/desktop/image-from-above.jpg") no-repeat center center/cover;
  }
}
.creations__info-grid .c6 {
  background: url("../../images/mobile/image-pocket-borealis.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c6 {
    background: url("../../images/desktop/image-pocket-borealis.jpg") no-repeat center center/cover;
  }
}
.creations__info-grid .c7 {
  background: url("../../images/mobile/image-curiosity.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c7 {
    background: url("../../images/desktop/image-curiosity.jpg") no-repeat center center/cover;
  }
}
.creations__info-grid .c8 {
  background: url("../../images/mobile/image-fisheye.jpg") no-repeat center center/cover;
}
@media screen and (min-width: 999px) {
  .creations__info-grid .c8 {
    background: url("../../images/desktop/image-fisheye.jpg") no-repeat center center/cover;
  }
}

.cta {
  width: 200px;
  height: 50px;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Josefin Sans", sans-serif;
  padding: 19px 47px;
  border: 0.5px solid #000;
  font-size: 17px;
  letter-spacing: 4px;
  margin-top: 0;
  transition: all 0.3s ease-in-out;
}
.cta:hover {
  color: #fff;
  background: #000;
}
@media screen and (min-width: 699px) {
  .cta {
    padding: 16px 50px;
  }
}

.desktop-cta {
  display: none;
}
@media screen and (min-width: 699px) {
  .desktop-cta {
    display: unset;
  }
}

.mobile-cta-wrapper {
  text-align: center;
  margin-top: 44px;
}

@media screen and (min-width: 699px) {
  .mobile-cta {
    display: none;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 3.8rem 2.5rem;
  background: black;
}
@media screen and (min-width: 699px) {
  .footer {
    flex-direction: row;
    align-items: start;
    justify-content: space-between;
    padding: 3rem 11.5%;
  }
}

.footer-logo {
  width: 75%;
  margin: auto;
  margin-bottom: 2.5rem;
}
@media screen and (min-width: 699px) {
  .footer-logo {
    width: 50%;
    margin: 0 auto;
    margin-bottom: 2rem;
  }
}

.page-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  margin-bottom: 3rem;
}
@media screen and (min-width: 699px) {
  .page-links {
    flex-direction: row;
    gap: 8%;
    margin: 0;
  }
}

.page-links li a {
  font-family: Alata;
  font-size: 0.95rem;
  color: white;
}

.page-links li a:hover {
  text-decoration: underline;
}

.social-links {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}
@media screen and (min-width: 699px) {
  .social-links {
    justify-content: end;
    margin-bottom: 2rem;
  }
}

.social-links a:hover img {
  transform: scale(120%);
}

.footer-copy {
  font-family: Alata;
  font-size: 0.9rem;
  color: #fff;
}